<template>
  <el-container>
    <el-header>
      <div class="header-container">
        <div class="left-menu">
          <el-dropdown trigger="click">
            <el-button plain class="icon-button">
              <el-icon>
                <Menu />
              </el-icon>
            </el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="getNotice">
                  <el-icon>
                    <Bell />
                  </el-icon>
                  
                </el-dropdown-item>
                <el-dropdown-item @click="goHome">
                  <el-icon>
                    <House />
                  </el-icon>
                  
                </el-dropdown-item>
                <el-dropdown-item @click="goClub">
                  <el-icon>
                    <Basketball />
                  </el-icon>
                  
                </el-dropdown-item>
                <el-dropdown-item @click="toggleNotification">
                  <el-icon>
                    <component
                      :is="notificationAvailable ? 'Bell' : 'MuteNotification'"
                    />
                  </el-icon>
                  
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>

        <div class="center-content">
          <h2></h2>
        </div>

        <div class="right-menu">
          <el-dropdown trigger="click">
            <el-button plain class="icon-button">
              <el-icon><Avatar /></el-icon>
            </el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="refresh" :disabled="isRefreshing">
                  <el-icon :class="{ 'is-loading': isRefreshing }">
                    <Refresh />
                  </el-icon>
                  
                </el-dropdown-item>
                <el-dropdown-item @click="switchUser">
                  <el-tooltip
                    content=""
                    placement="top"
                    open-delay="500"
                  >
                    <el-icon>
                      <Switch />
                    </el-icon>
                  </el-tooltip>
                  
                </el-dropdown-item>
                <el-dropdown-item @click="logout">
                  <el-tooltip
                    content=""
                    placement="top"
                    open-delay="500"
                  >
                    <el-icon color="red">
                      <CloseBold />
                    </el-icon>
                  </el-tooltip>
                  
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </div>
    </el-header>

    <el-main v-if="showMainBoard">
      <div v-if="user" class="stuInfo">
        <h1 @click="toggleMask" :class="{ masked: !masked }">
          {{ user.studentName }}
        </h1>
        <p @click="toggleMask" :class="{ masked: !masked }">
          {{ user.registerCode }}
        </p>
      </div>
      <div v-else class="stuInfo">
        <h1>
          <el-icon class="is-loading">
            <Loading />
          </el-icon>
        </h1>
        <p>
          <el-icon class="is-loading">
            <Loading />
          </el-icon>
        </p>
      </div>

      <div>
        <div class="runInfo">
          <el-row :gutter="20" justify="center">
            <el-col :span="24">
              <el-card class="no-border-card">
                <!--  -->
                <el-progress
                  v-if="activity"
                  :percentage="activity.club_completion_percentage"
                  :stroke-width="20"
                  :text-inside="true"
                  @click="getActivity"
                >
                  <span> {{ activity.club_completion_rate }}</span>
                </el-progress>
                <el-progress
                  v-else
                  :percentage="100"
                  :stroke-width="20"
                  :text-inside="true"
                  :duration="3"
                  striped
                  striped-flow
                >
                  <span
                    ><el-icon class="is-loading"><Loading /></el-icon
                  ></span>
                </el-progress>

                <!--  -->
                <el-progress
                  v-if="activity"
                  :percentage="activity.running_completion_percentage"
                  :stroke-width="20"
                  :text-inside="true"
                  @click="getActivity"
                >
                  <span
                    > {{ activity.running_completion_rate }}</span
                  >
                </el-progress>
                <el-progress
                  v-else
                  :percentage="100"
                  :stroke-width="20"
                  :text-inside="true"
                  :duration="3"
                  striped
                  striped-flow
                >
                  <span
                    ><el-icon class="is-loading"><Loading /></el-icon
                  ></span>
                </el-progress>

                <!--  -->
                <el-progress
                  v-if="runInfo"
                  :percentage="runInfo.runDistanceCompletionPercentage"
                  :stroke-width="20"
                  :text-inside="true"
                  @click="getRunInfo"
                >
                  <span
                    > {{ runInfo.runDistanceCompletionRate }}</span
                  >
                </el-progress>
                <el-progress
                  v-else
                  :percentage="100"
                  :stroke-width="20"
                  :text-inside="true"
                  :duration="3"
                  striped
                  striped-flow
                >
                  <span
                    ><el-icon class="is-loading"><Loading /></el-icon
                  ></span>
                </el-progress>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>

      <el-divider />
      <div>
        <div class="input-group">
          <el-input-number
            for="runDistance"
            id="distance"
            v-model="runDistance"
            :min="1000"
            :max="5000"
            :step="100"
            style="width: 200px"
            placeholder=""
          ></el-input-number>
        </div>
        <div class="input-group">
          <el-input-number
            for="runTime"
            id="time"
            v-model="runTime"
            :min="30"
            :max="100"
            :step="5"
            style="width: 200px"
            placeholder=""
          ></el-input-number>
        </div>
        <div class="input-group">
          <el-select id="map" v-model="mapChoice" placeholder="">
            <el-option
              label=""
              value="cuit_lqy"
            ></el-option>
            <el-option
              label=""
              value="cuit_hkg"
            ></el-option>
            <el-option
              label=""
              value="cdutcm_wj"
            ></el-option>
          </el-select>
        </div>
        <el-button @click="randomizeInputs" plain class="icon-button">
          <el-icon class="is-loading">
            <RefreshRight />
          </el-icon>
        </el-button>
      </div>

      <el-divider />
      <el-button
        type="primary"
        :loading="isSubmitting"
        @click="submitActivityData"
        :disabled="LoginState"
        round
        ></el-button
      >
    </el-main>
  </el-container>
</template>


<script setup>
import "@/styles/dashboard/index.css";
import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";

import {
  useUser,
  useActivity,
  useRunInfo,
  useSubmitActivity,
} from "@/hooks/dashboard/index";
import useNotice from "@/hooks/notice/";

const { getNotice } = useNotice();

const { user, fetchUser } = useUser();
const { activity, fetchActivity } = useActivity();
const { runInfo, fetchRunInfo } = useRunInfo();
const { isSubmitting, submit } = useSubmitActivity();

const userData = JSON.parse(localStorage.getItem("userData")) || null;
const token = localStorage.getItem("token") || null;

const showMainBoard = ref(true);
const masked = ref(true);

const goHome = () => {
  router.push("/");
};

const router = useRouter();
const runDistance = ref(null);
const runTime = ref(null);
const mapChoice = ref(null);
const LoginState = ref(true);

// 
const getActivity = async () => {
  await fetchActivity(user.value.schoolId, user.value.studentId);
};

// 
const getRunInfo = async () => {
  await fetchRunInfo(user.value.userId, user.value.schoolId);
};

// 
const isRefreshing = ref(false);
const refresh = async () => {
  isRefreshing.value = true;
  await Promise.all([getActivity(), getRunInfo()]);
  isRefreshing.value = false;
};

// 
const submitActivityData = async () => {
  if (!runDistance.value || !runTime.value || !mapChoice.value) {
    ElMessage.error("");
    return;
  }
  const schoolId = userData.schoolId;
  const userId = userData.userId;
  const result = await submit(
    runDistance.value,
    runTime.value,
    mapChoice.value,
    schoolId,
    userId
  );
  if (result) {
    refresh();
  }
};

// 
const goClub = () => {
  router.push("/club");
};

// 
const randomizeInputs = () => {
  const schoolName = userData.schoolName;
  let maps = [];

  if (schoolName === "") {
    maps = ["cuit_lqy", "cuit_hkg"];
  } else if (schoolName === "") {
    maps = ["cdutcm_wj"];
  }

  runDistance.value = Math.floor(Math.random() * (6000 - 1000 + 1)) + 1000;
  runTime.value = Math.floor(Math.random() * (100 - 30 + 1)) + 30;
  mapChoice.value = maps[Math.floor(Math.random() * maps.length)];
};

// 
const switchUser = () => {
  ElMessage.info("");
};

const notificationAvailable = ref(
  localStorage.getItem("NotificationAvailable") !== "false"
);

const toggleNotification = () => {
  const newValue = !notificationAvailable.value;
  localStorage.setItem("NotificationAvailable", newValue);
  notificationAvailable.value = newValue;
};

onMounted(async () => {
  if (notificationAvailable.value) {
    getNotice();
  }
  if (!userData || !token) {
    router.push("/home");
  } else {
    const result = await fetchUser();
    if (result) {
      LoginState.value = false;
      refresh();
    }
  }
});

// 
const logout = () => {
  localStorage.clear();
  router.push("/home");
  ElMessage.info("");
};

// 
const toggleMask = () => {
  masked.value = !masked.value;
};
</script>

<style scoped>
.el-card {
  border: none;
  box-shadow: none;
}
</style>